  showprice(price) {
    var check_price = Number.isInteger(parseFloat(price))
    if (check_price == false) {
      return parseFloat(price).toFixed(2)
    } else {
      return price
    }
  }

  enterprice(ev: any) {
    const pattern = /[0-9.,]/;
    let inputchar = String.fromCharCode(ev.charCode);

    if (!pattern.test(inputchar)) {
      ev.preventDEfault();
    }
  }

